












import Vue from 'vue';
import Component from 'vue-class-component';
import LoadingDirective from '../../index';

Vue.use(LoadingDirective);
@Component({
  name: 'Demo1'
})
export default class Demo1 extends Vue {

  public loading = false;
  public loading2 = false;

  public load() {
    this.loading = true;
    this.loading2 = true;
    setTimeout(() => {
      this.loading = false;
    }, 2100);
    setTimeout(() => {
      this.loading2 = false;
    }, 2099);
  }
}
